<template>
  <header class="header">
    <!--  -->
    <div class="top">
      <div class="container">
        <div class="loginList">
          <p></p>
          <p>
            <span></span>
            <router-link to="/login"></router-link>
            <router-link to="/register" class="register"></router-link>
          </p>
        </div>
        <div class="typeList">
          <a href="###"></a>
          <a href="###"></a>
          <a href="###"></a>
          <a href="###"></a>
          <a href="###"></a>
          <a href="###"></a>
          <a href="###"></a>
          <a href="###"></a>
        </div>
      </div>
    </div>
    <!-- -->
    <div class="bottom">
      <h1 class="logoArea">
        <router-link class="logo" to="/home">
          <img src="./images/logo.png" alt="" />
        </router-link>
      </h1>
      <div class="searchArea">
        <form action="###" class="searchForm">
          <input
            type="text"
            id="autocomplete"
            class="input-error input-xxlarge"
            v-model="keyWord"
          />
          <button
            class="sui-btn btn-xlarge btn-danger"
            type="button"
            @click="goSearch"
          >
            
          </button>
        </form>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  data() {
    return {
      keyWord: "",
    };
  },
  methods: {
    goSearch() {
      // 
      // :
      // this.$router.push('/search/' + this.keyWord + '?keyWord=' + this.keyWord.toUpperCase())
      // :
      // this.$router.push(`/search/${this.keyWord}?keyWord=${this.keyWord.toUpperCase()}`)
      // :
      // this.$router.push({
      //   name: "search",
      //   params: { keyWord: this.keyWord || undefined },
      //   query: { keyWord: this.keyWord },
      // });
      let location = {
        name: "search",
        params: { keyword: this.keyWord || undefined },
      };
      // routequeryquery
      if (this.$route.query) {
        location.query = this.$route.query;
      }
      this.$router.push(location);
    },
  },
  watch: {
    $route: {
      handler() {
        if(this.$route.params.keyword == undefined){
          this.keyWord = ""
        }
      }
    }
  }
};
</script>

<style scoped lang='less'>
.header {
  & > .top {
    background-color: #eaeaea;
    height: 30px;
    line-height: 30px;

    .container {
      width: 1200px;
      margin: 0 auto;
      overflow: hidden;

      .loginList {
        float: left;

        p {
          float: left;
          margin-right: 10px;

          .register {
            border-left: 1px solid #b3aeae;
            padding: 0 5px;
            margin-left: 5px;
          }
        }
      }

      .typeList {
        float: right;

        a {
          padding: 0 10px;

          & + a {
            border-left: 1px solid #b3aeae;
          }
        }
      }
    }
  }

  & > .bottom {
    width: 1200px;
    margin: 0 auto;
    overflow: hidden;

    .logoArea {
      float: left;

      .logo {
        img {
          width: 175px;
          margin: 25px 45px;
        }
      }
    }

    .searchArea {
      float: right;
      margin-top: 35px;

      .searchForm {
        overflow: hidden;

        input {
          box-sizing: border-box;
          width: 490px;
          height: 32px;
          padding: 0px 4px;
          border: 2px solid #ea4a36;
          float: left;

          &:focus {
            outline: none;
          }
        }

        button {
          height: 32px;
          width: 68px;
          background-color: #ea4a36;
          border: none;
          color: #fff;
          float: left;
          cursor: pointer;

          &:focus {
            outline: none;
          }
        }
      }
    }
  }
}
</style>