<script>
import ImageList from "./ImageList/ImageList";
import Zoom from "./Zoom/Zoom";
// vuex
import { mapGetters } from "vuex";

export default {
  name: "Detail",
  data() {
    return {
      skuNum: 1,
    };
  },
  components: {
    ImageList,
    Zoom,
  },
  methods: {
    // 
    changeChecked(arr, clickArr) {
      arr.spuSaleAttrValueList.forEach((item) => {
        item.isChecked = 0;
      });
      clickArr.isChecked = 1;
    },
    // 
    changeSkuNum(event) {
      let changeNum = event.target.value * 1;
      if (isNaN(changeNum) || changeNum < 1) {
        this.skuNum = 1;
      } else {
        this.skuNum = Math.ceil(changeNum);
      }
    },
    addShoppingCart() {
      let result = this.$store.dispatch("addOrUpdateInShoppingCart", {
        skuId: this.$route.params.skuId,
        skuNum: this.skuNum,
      });
      // asyncpromise
      result
        .then((req) => {
          this.$router.push({ name: "addcartsuccess", query: {skuNum: this.skuNum}});
          sessionStorage.setItem("SKUINFO", JSON.stringify(this.skuInfo))
          sessionStorage.setItem("SPUSALEATTRLIST", JSON.stringify(this.spuSaleAttrList))
        })
        .catch((error) => {
          console.log(error.message);
        });
    },
  },
  mounted() {
    this.$store.dispatch("getGoodInfo", this.$route.params.skuId);
  },
  computed: {
    ...mapGetters(["categoryView", "skuInfo", "spuSaleAttrList"]),
    // undefined
    skuImageList() {
      return this.skuInfo.skuImageList || [];
    },
  },
};
</script>

